.catalog {
  padding: vw(22px) 0;
  background: #F9F0EA;

  &__list {
    margin: vw(-32px) vw(-16px);
    display: flex;
    flex-wrap: wrap;
  }

  &__li {
    padding: vw(32px) vw(16px);
    width: calc(100% / 5);
    position: relative;

    &:hover {
      z-index: 5;
    }
  }
}
